@import "./util/variables.scss";

.catalog {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  row-gap: 46px;
  column-gap: 48px;
  justify-content: center;

  @media (min-width: 1024px) {
    padding: 50px 40px;
  }

  @media (min-width: 1200px) {
    padding: 50px 128px;
  }
}

.card {
  padding: 32px 16px 0;
  width: 200px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  transition: transform $transition-speed;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    margin-left: 3px;
    width: 160px;
    height: 134px;
    display: block;
  }

  &__product-title {
    margin-top: 40px;
    margin-bottom: 0;
    font-size: 12px;
    line-height: 18px;
    color: $main-text-color;
    font-weight: 500;

    &:hover {
      color: $card-title-color;
    }
  }

  &__product-code {
    margin-top: 4px;
    margin-right: auto;
    font-size: 10px;
    line-height: 14px;
    color: $secondary-text-color;
  }

  &__ratings {
    display: flex;
    align-items: center;
    margin-top: 16px;
    justify-content: space-between;
  }

  &__reviews {
    font-size: 10px;
    line-height: 14px;
    font-style: normal;
    color: $main-text-color;
  }

  &__price {
    display: flex;
    align-items: center;
    margin-top: 24px;
    color: $main-text-color;
    justify-content: space-between;

    &-text {
      font-size: 12px;
      line-height: 18px;
      color: $secondary-text-color;
    }

    &-number {
      font-size: 16px;
      font-weight: bold;
      line-height: 18px;
      margin-left: 85px;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 16px;
    margin-bottom: 16px;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    background-color: $main-button-color;

    color: $secondary-button-color;
    font-size: 14px;
    line-height: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    border: 1px solid transparent;

    &:hover {
      background-color: $secondary-button-color;
      border-color: $main-button-color;
      color: $main-button-color;
    }
  }
}
